/**
 * 资源 key 注入脚本
 *
 * 核心思路：资源条目（[res]资源库）在世界书中设为【关闭】状态，
 * 这样完整的 URL 不会作为提示词发给 AI 浪费 token。
 *
 * 本脚本在加载时：
 * 1. 通过 getWorldbook 读取关闭态的资源条目（API 能读到所有条目，无论开关）
 * 2. 解析 YAML，提取所有资源 key 名称（不含 URL）
 * 3. 用 injectPrompts 将紧凑的 key 列表注入给 AI
 *
 * 这样 AI 能看到可用的资源 key（如背景图名、角色表情名），
 * 而前端界面通过同样的 getWorldbook 读取完整 URL 来加载资源。
 * 两边各取所需，token 消耗降到最低。
 */

import * as YAML from 'yaml';

const RESOURCE_ENTRY_NAME = '[res]资源库';
const INJECT_ID = 'wasteland-echoes-resource-keys';

interface RawResourceYaml {
  角色立绘?: Record<string, Record<string, string>>;
  背景图?: Record<string, string>;
  BGM?: Record<string, string>;
  CG?: Record<string, string>;
}

/** 从 YAML 内容提取 key 列表，生成紧凑文本 */
function buildKeysSummary(yamlContent: string): string | null {
  try {
    const raw = YAML.parse(yamlContent) as RawResourceYaml;
    if (!raw) return null;

    const lines: string[] = ['[可用资源key]'];

    if (raw.角色立绘) {
      const parts = Object.entries(raw.角色立绘).map(([name, faces]) => `${name}(${Object.keys(faces).join(',')})`);
      if (parts.length) lines.push(`角色立绘: ${parts.join(', ')}`);
    }

    if (raw.背景图) {
      const keys = Object.keys(raw.背景图);
      if (keys.length) lines.push(`背景图: ${keys.join(', ')}`);
    }

    if (raw.BGM) {
      const keys = Object.keys(raw.BGM);
      if (keys.length) lines.push(`BGM: ${keys.join(', ')}`);
    }

    if (raw.CG) {
      const keys = Object.keys(raw.CG);
      if (keys.length) lines.push(`CG: ${keys.join(', ')}`);
    }

    return lines.length > 1 ? lines.join('\n') : null;
  } catch (e) {
    console.error('[资源key注入] YAML 解析失败:', e);
    return null;
  }
}

/** 从世界书中查找资源条目内容 */
async function findResourceContent(): Promise<string | null> {
  const charWorldbooks = getCharWorldbookNames('current');
  const worldbookNames: string[] = [];

  if (charWorldbooks.primary) worldbookNames.push(charWorldbooks.primary);
  worldbookNames.push(...charWorldbooks.additional);
  worldbookNames.push(...getGlobalWorldbookNames());

  const uniqueNames = [...new Set(worldbookNames)];

  for (const wbName of uniqueNames) {
    try {
      const entries = await getWorldbook(wbName);
      const entry = entries.find(e => e.name === RESOURCE_ENTRY_NAME || e.name.includes('[res]'));
      if (entry?.content) {
        console.info(`[资源key注入] 找到资源条目 "${entry.name}" (enabled=${entry.enabled}) in "${wbName}"`);
        return entry.content;
      }
    } catch {
      continue;
    }
  }
  return null;
}

async function init() {
  const content = await findResourceContent();
  if (!content) {
    console.warn('[资源key注入] 未找到资源条目，跳过注入');
    return;
  }

  const summary = buildKeysSummary(content);
  if (!summary) {
    console.warn('[资源key注入] 资源条目为空或解析失败，跳过注入');
    return;
  }

  const { uninject } = injectPrompts([
    {
      id: INJECT_ID,
      position: 'in_chat',
      depth: 1,
      role: 'system',
      content: summary,
    },
  ]);

  console.info(`[资源key注入] 已注入资源 key 列表 (${summary.length} chars)`);

  $(window).on('pagehide', () => {
    uninject();
  });
}

$(() => {
  errorCatched(init)();
});
